import { Form, Button, Input, DatePicker, Row, Select } from "antd";
import { Option } from "antd/lib/mentions";
import moment from "moment";
import React, { useState } from "react";
import { useTypedSelector } from "../hooks/useTypedSelector";
import { formatDate } from "../utils/date";
import { rules } from "../utils/rules";

const EventForm = (props) => {
  const [event, setEvent] = useState({
    author: "",
    date: "",
    description: "",
    guest: "",
  });
  const { user } = useTypedSelector((state) => state.auth);

  const selectDate = (date) => {
    if (date) {
      setEvent({ ...event, date: formatDate(date.toDate()) });
    }
  };

  const submitForm = () => {
    props.submit({ ...event, author: user.username });
    console.log("submitForm");
  };

  return (
    <Form onFinish={submitForm}>
      <Form.Item
        label="Описание события"
        name="description"
        rules={[rules.required("Обязательное")]}
      >
        <Input
          onChange={(e) => {
            setEvent({ ...event, description: e.target.value });
          }}
          value={event.description}
        />
      </Form.Item>
      <Form.Item
        label="Дата события"
        name="date"
        rules={[
          rules.required("Обязательное"),
          //rules.isDateAfter("Прошлая дата"),
        ]}
      >
        <DatePicker onChange={(data) => selectDate(data)} />
      </Form.Item>
      <Form.Item
        label="Выберите гостя"
        name="guest"
        rules={[rules.required("Обязательное")]}
      >
        <Select
          style={{ width: 120 }}
          onChange={(guest) => {
            setEvent({ ...event, guest: guest });
          }}
        >
          {props.guests.map((guest) => (
            <Select.Option
              key={guest.username}
              value={guest.username}
            >
              {guest.username}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>
      <Row justify="end">
        <Form.Item>
          <Button type="primary" htmlType="submit">
            Добавить
          </Button>
        </Form.Item>
      </Row>
    </Form>
  );
};

export default EventForm;
