import React from "react";
import { Layout, Row } from "antd";
import LoginForm from "../components/LoginForm";
import Card from "antd/lib/card/Card";

const Login = () => {
  return (
    <div>
      <Layout>
        <Row justify="center" align="middle" className="h100">
          <Card>
            <LoginForm />
          </Card>
        </Row>
      </Layout>
    </div>
  );
};

export default Login;
